}


@keyframes introTitle{
  0%{
    transform: translateY(-20px);
  }
  100%{
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes introButton{
  0%{
    transform: translateY(30px);
  }
  100%{
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes upndown{
  0%{
      transform: translateY(-5px);
      opacity: 0;
  }
  50%{
      opacity: 1;
  }
  100%{
      opacity: 0;
      transform: translateY(10px);
  }
}

