/*--------------------------------------------------------------
# Project
--------------------------------------------------------------*/

.projectCardStyle{
  margin-top: 5%;
}


.projectCard{
  height: 500px;
  position: relative;
  margin-bottom: 50px;
  transform-style: preserve-3d;
}

.projectCard .face{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transform-style: preserve-3d;
  display: flex;
  justify-content: center;
  align-items: center;
  backface-visibility: hidden;
  transition: 0.8s ease-in-out;
  transform: perspective(500px) rotateY(0deg);
}

.projectCard .face.front{
  -webkit-box-shadow: 0px 10px 39px -12px rgba(0,0,0,0.77);
-moz-box-shadow: 0px 10px 39px -12px rgba(0,0,0,0.77);
box-shadow: 0px 10px 39px -12px rgba(0,0,0,0.77);
}

.projectCard:hover .face.front{
  transform: perspective(500px) rotateY(180deg);
}

.projectCard .face.back{
  -webkit-box-shadow: 0px 10px 39px -12px rgba(0,0,0,0.77);
-moz-box-shadow: 0px 10px 39px -12px rgba(0,0,0,0.77);
box-shadow: 0px 10px 39px -12px rgba(0,0,0,0.77);
  color: #fff;
  transform: perspective(500px) rotateY(180deg);
}


