  box-shadow: 1px 13px 20px -2px rgba(0,0,0,0.58);
}



/* End Every Project Back */


.projectCard:hover .face.back{
  transform: perspective(500px) rotateY(360deg);
}

.projectCard .face h2{
  font-size: 4em;
  text-transform: uppercase;
  transform: perspective(500px) translateZ(50px);
}


